.mywork{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 80px;
    margin: 80px 170px;
}
.mywork-title {
    position: relative;
}
.mywork-title h1{
    padding: 0px 30px;
    font-size: 80px;
    font-weight: 600;
}
.mywork-title img{
    position:absolute;
    bottom: 0;
    right: 0;
    z-index: -1; 
}
.mywork-container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 40px;
}
.mywork-container img{
    box-sizing: border-box;
    width: 419px;
    height: 280px;
    transition: 0.3s;
    cursor: pointer;
}
.mywork-container img:hover{
    transform: scale(1.10);
    border: 4px solid #ff00ff;
    border-radius: 10px;
    transition: 0.3s;
}
.mywork-showmore{
    display: flex;
    gap: 15px;
    border-radius: 50px;
    border: 2px solid white;
    padding: 30px 60px;
    font-size: 22px;
    font-weight: 500;
    margin-bottom: 80px;
    transition: 0.6s;
    cursor: pointer;
}.mywork-showmore:hover{
    background:linear-gradient(45deg,#3f0028,#582300);
    gap: 30px;
    transition: 0.6s;
}

/* Updated CSS for mobile responsiveness */
@media (max-width: 768px) {
    .mywork {
        gap: 40px; /* Reduced gap */
        margin: 50px 20px; /* Adjusted margin */
    }

    .mywork-title h1 {
        font-size: 40px; /* Reduced font size */
        padding: 0 20px; /* Adjusted padding */
    }

    .mywork-title img {
        width: 60px; /* Reduced width */
        right: -10px; /* Adjusted position */
    }

    .mywork-container {
        grid-template-columns: 1fr; /* Changed to single column layout */
        gap: 20px; /* Reduced gap */
    }

    .mywork-container img {
        width: 100%; /* Adjusted width */
        height: auto; /* Ensured aspect ratio */
        border: 2px solid transparent; /* Removed border */
        border-radius: 0; /* Removed border radius */
    }

    .mywork-container img:hover {
        transform: none; /* Removed scale transformation */
    }

    .mywork-showmore {
        padding: 20px 40px; /* Adjusted padding */
        font-size: 18px; /* Reduced font size */
    }
}
